html,
body {
  background: #F2F6FC;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding-bottom: 60px; }
  .App .todo-list-title {
    color: #40ECFF;
    font-style: italic; }

.todo-list-item {
  padding: 0 20px 0 15px;
  box-sizing: border-box;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  border-bottom: 1px solid #ededed;
  color: #4F5E79;
  line-height: 60px;
  height: 60px;
  font-size: 22px;
  font-weight: 200;
  background: #fff; }
  .todo-list-item.checked {
    text-decoration: line-through;
    color: #C0C4CC; }
    .todo-list-item.checked .item-check-dot {
      background-color: #40ECFF; }
  .todo-list-item .todo-delete-btn {
    display: none;
    position: absolute;
    right: 20px;
    cursor: pointer; }
  .todo-list-item:hover .todo-delete-btn {
    display: block; }
  .todo-list-item .item-check-dot {
    cursor: pointer;
    margin-right: 15px;
    width: 24px;
    height: 24px;
    border-radius: 100%;
    border: 2px solid #40ECFF; }
  .todo-list-item .item-content {
    padding-right: 15px;
    box-sizing: border-box; }
    .todo-list-item .item-content.done {
      color: #DCDFE6; }
    .todo-list-item .item-content.editing {
      padding-left: 43px; }
      .todo-list-item .item-content.editing .item-input {
        background: #fff;
        border: none;
        font-size: 22px;
        font-weight: 200;
        outline: none; }

.todo-list-footer {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  position: relative;
  padding: 0 15px;
  box-sizing: border-box;
  height: 40px;
  line-height: 40px;
  color: #4F5E79;
  font-size: 14px;
  background: #fff; }
  .todo-list-footer .filter-tabs {
    position: absolute;
    left: 0;
    right: 0; }
    .todo-list-footer .filter-tabs button.filter-tab {
      cursor: pointer;
      border: none;
      color: #4F5E79;
      font-weight: 200;
      outline: none;
      font-size: 14px;
      border: 1px solid #fff; }
      .todo-list-footer .filter-tabs button.filter-tab:hover {
        border: 1px solid #F2F6FC; }
      .todo-list-footer .filter-tabs button.filter-tab.active {
        border-color: #40ECFF; }
  .todo-list-footer button.clear-completed {
    z-index: 1;
    cursor: pointer;
    outline: none;
    font-weight: 200;
    font-size: 14px;
    border: none;
    cursor: pointer;
    color: #4F5E79;
    background: transparent; }
    .todo-list-footer button.clear-completed:hover {
      text-decoration: underline; }

.todo-list {
  position: relative;
  width: 500px;
  background: #fff;
  box-shadow: 0 6px 8px 0 rgba(57, 71, 108, 0.1);
  font-weight: 200; }
  .todo-list .todo-input {
    color: #303133;
    padding-left: 60px;
    padding-right: 20px;
    display: block;
    width: 100%;
    font-size: 22px;
    box-sizing: border-box;
    outline: none;
    border: none;
    font-weight: 200;
    background: #fff;
    box-shadow: 0 6px 8px 0 rgba(57, 71, 108, 0.1);
    height: 60px;
    line-height: 60px;
    border-bottom: 1px solid #ededed; }
    .todo-list .todo-input::-webkit-input-placeholder {
      /* WebKit browsers */
      color: #eee;
      font-style: italic; }
    .todo-list .todo-input:-moz-placeholder {
      /* Mozilla Firefox 4 to 18 */
      color: #eee;
      font-style: italic; }
    .todo-list .todo-input::-moz-placeholder {
      /* Mozilla Firefox 19+ */
      color: #eee;
      font-style: italic; }
    .todo-list .todo-input:-ms-input-placeholder {
      /* Internet Explorer 10+ */
      color: #eee;
      font-style: italic; }

